@import url('https://fonts.googleapis.com/css2?family=DM+Sans&display=swap');

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

.header{
position: absolute;
width: 1440px;
height: 140px;
left: 0px;
top: 0px;
background: #292929;
}

.border{
position: absolute;
width: 193px;
height: 60px;
left: 77px;
top: 40px;
text-align: center;
background: #292929;
color: #FFFFFF;
border: 1px solid #FFFFFF;
}

.text-container{
position: absolute;
width: 156.7px;
height: 32.94px;
left: 19px;
top: 12px;
font-size: 30px;
font-weight: lighter;
}

.hero{
position: absolute;
width: 1440px;
height: 550px;
left: 0px;
top: 138px;
background: url('../img/movie-app.jpg') no-repeat center/cover;
}

.text{
  position: relative;
  width: 490px;
  height: 282px;
  left: 77px;
  top: 107px;  
  font-family: 'DM Sans';
  font-style: normal;
  font-weight: 700;
  font-size: 72px;
  line-height: 93.74px;
  letter-spacing: -0.05em;  
  color: #FFFFFF;
}

.search{
position: absolute;
width: 77px;
height: 31px;
left: 77px;
top: 751px;
}

.search p{
  font-size: 28px;
  font-family: 'DM Sans';
}

.search-input{
position: absolute;
width: 1306px;
height: 54px;
top: 31px;
border: 1px solid #000000;
box-sizing: border-box;
flex: none;
font-size: 30px;
order: 1;
flex-grow: 0;
margin: 4px 0px;
}

.movie-cat1{
position: absolute;
width: 279px;
height: 31px;
left: 67px;
top: 888px;
font-family: 'DM Sans';
font-style: normal;
font-weight: 400;
font-size: 24px;
line-height: 31px;
color: #000000;
}

.container .movie-cat2{
position: absolute;
width: 279px;
height: 31px;
left: 67px;
top: 1285px;
font-family: 'DM Sans';
font-style: normal;
font-weight: 400;
font-size: 24px;
line-height: 31px;
color: #000000;
order: 1;
}

.container .bot{
  position: absolute;
  top: 1703px;
  padding:10px;
}

.container .movie{
display: flex;
flex-direction: row;
justify-content: flex-start;
padding: 10px;
flex-grow: 1;
flex-wrap: nowrap;
max-width: 1440px;
margin-bottom: 48px;
}

.movie-items img{
position: relative;
width: 300px;
height: 300px;
left: 42px;
top: 937px;
background: #000000;
border-radius: 12px;
flex: none;
order: 0;
flex-grow: 0;
margin: 0px 13px;
}

.movie-items h5{
position: relative;
text-align: center;
padding-top: 2px;
width: 290px;
height: 20px;
left: 60px;
top: 760px;
font-family: 'DM Sans';
font-style: normal;
font-weight: 400;
font-size: 24px;
line-height: 31px;
color: #FFFFFF;
}

@media(max-width: 834px){
  .header{
    width: 834px;   
    }

  .border{
    left: 321px;     
    }

  .hero{
    width: 834px;
  }

  .text{
    top: 130px;
    left: 172px;
    text-align: center;
  }

  .search-input{
    width: 680px;
    font-size: 16px;
  }

}


